export function isAvailable(item) {
    return isAvailableMonth(item) && isAvailableTime(item);
}

export function availability(item) {
    var availableMonth = isAvailableMonth(item);
    if (!availableMonth) return "notAvailable";
    
    var availableTime = isAvailableTime(item);

    if (availableTime) return "availableTime";
    else return "availableMonth";
}

export function isAvailableMonth(item, currentDate = new Date(Date.now())) {
    var { monthsAvailable } = item;
    var monthNumber = currentDate.getMonth() + 1;
    
    return monthsAvailable.includes(monthNumber);
}

export function isAvailableTime(item, currentDate = new Date(Date.now())) {
    var { times } = item;
    var hour = currentDate.getHours();

    for (var timeRange of times) {
        let [startTime, endTime] = timeRange;

        if (hour > startTime && hour < endTime) return true;
    }

    return false;
}

export function isLeaving(item, currentDate = new Date(Date.now())) {
    if (!isAvailable(item)) return false;

    var month = currentDate.getMonth() + 2;
    if (month === 13) month = 1;

    return !item.monthsAvailable.includes(month);
}