<div class="songItem">
    <img src={item.image} alt="" loading="lazy">
</div>

<script>
// Props
export var item = {};
export var open = false;

// Function
function openDetails() {
    open = true;
}
</script>

<style lang="scss">

.songItem {
    height: 100%;
    width: 100%;

    img {
        height: 100%;
        width: 100%;

        display: block;
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

</style>