import { writable } from "svelte/store";

export const donatedList = writable([]);
export const donateMultiple = writable(false);

if (localStorage.getItem("donatedItems")) {
    donatedList.update(_ => {
        return JSON.parse(localStorage.getItem("donatedItems"))
    });
}

donatedList.subscribe(val => localStorage.setItem("donatedItems", JSON.stringify(val)));