<div class="villagerPassport">
    <div class="header">
        <p>
            <span class="floater">
                <span class="line"></span> <span>Passport</span> <span class="line"></span>
            </span>
        </p>
    </div>
    <div class="mainSection">
        <img class="treeImage" src="./img/island-crop.png" alt="">
        <div class="generalInformationGrid">
            <img src={villager.image} alt="">
            <div class="textSection">
                <div class="catchPhrase">{villager.catchPhrase}</div>
                <h3>{villager.name}</h3>
                <p><b>Species:</b> {villager.species}</p>
                <p><b>Personality:</b> {villager.personality}</p>
                <p><b>Born on:</b> {villager.birthDate.string}</p>
            </div>
        </div>
    </div>
    <div class="footer">
        <p>
            <span class="right">{bottomCode}</span>
        </p>
    </div>
</div>

<script>
// Props
export var villager = {};

// Computed
$: bottomCode = (" " + villager.name.toUpperCase() + " <<").padStart(14, "<");
</script>

<style lang="scss">

.villagerPassport {
    border-radius: 16px;
    box-shadow: 0 0 9px #00000033;

    .header {
        height: 48px;
        width: 100%;

        position: relative;

        border-top-left-radius: inherit;
        border-top-right-radius: inherit;
        background-image: url(../img/header-background.png);

        p {
            $textColor: #a29ca5;

            width: 100%;

            position: absolute;
            margin: 0;
            top: 50%;
            transform: translateY(-50%);

            font-size: 18px;
            font-weight: 500;
            text-align: center;
            color: $textColor;

            .floater {
                display: inline-block;
                margin: 0 auto;

                span {
                    float: left;
                }

                span.line {
                    height: 1px;
                    width: 32px;

                    margin: 11px 16px;

                    background-color: $textColor;
                }
            }
        }
    }

    .mainSection {
        width: 100%;

        position: relative;

        background-image: url(../img/main-background.png);
        margin: -1px 0;
        padding: 16px 0;

        $imageSize: 96px;

        .treeImage {
            width: 124px;
            right: 8px;

            display: block;
            position: absolute;
            bottom: 0;
        }

        .generalInformationGrid {
            display: grid;
            grid-template-columns: $imageSize + 16px auto;

            position: relative;

            padding: 0 8px;
            grid-gap: 8px;

            img {
                height: $imageSize;
                width: $imageSize;

                display: block;
                margin: 16px auto;
                border: 8px solid #f8f2df;
                border-radius: 24px;
            }

            .textSection {
                padding-top: 24px;

                h3 {
                    font-size: 24px;
                    margin: 0;
                }

                p {
                    margin: 4px 0;
                }

                .catchPhrase {
                    position: absolute;
                    top: -8px;
                    padding: 4px 8px;

                    background-color: #f7f1dd;
                    border-radius: 10px;
                    border: 1px solid #cccccc;
                    transform: rotate(-2deg);
                }
            }
        }
    }

    .footer {
        height: 48px;
        width: 100%;

        position: relative;

        border-bottom-left-radius: inherit;
        border-bottom-right-radius: inherit;
        background-image: url(../img/footer-background.png);

        p {
            width: calc(100% - 48px);

            position: absolute;
            margin: 0;
            top: 50%;
            transform: translateY(-50%);
            padding: 0 24px;

            font-size: 14px;
            color: #b2aa89;

            .right {
                display: block;
                float: right;

                font-weight: 700;
                font-family: monospace;
            }
        }
    }
}

</style>