import { Drawable, Stroke, Fill, CenterPoint, Rotatable } from "./screen.js";

export function Pixel(x, y) {
    const rectangleObject = Rectangle(x, y, 1, 1);

    return {
        ...rectangleObject,
    }
}

export function Circle(x, y, r) {
    const drawableObject = Drawable(x, y);
    const strokeObject = Stroke();
    const fillObject = Fill();

    return {
        r,
        ...drawableObject,
        ...strokeObject,
        ...fillObject,
        strokeDraw: strokeObject.draw,
        fillDraw: fillObject.draw,
        test() {
            console.log(this.y);
        },
        draw(ctx) {
            const { x, y, r } = this;

            this.strokeDraw(ctx);
            this.fillDraw(ctx);

            ctx.beginPath();
            ctx.arc(x, y, Math.abs(r), 0, Math.PI * 2);
            ctx.stroke();
            ctx.fill();
        }
    }
}

export function Line(x1, y1, x2, y2) {
    const strokeObject = Stroke();
    const rotatableObject = Rotatable(0);

    return {
        ...strokeObject,
        ...rotatableObject,
        beginPosition: {
            x: x1,
            y: y1,
        },
        endPosition: {
            x: x2,
            y: y2,
        },
        draw(ctx) {
            var { beginPosition, endPosition } = this;

            ctx.beginPath();
            ctx.moveTo(beginPosition.x, beginPosition.y)
            ctx.lineTo(endPosition.x, endPosition.y);
            ctx.stroke();
        }
    }
}

export function Rectangle(x, y, w, h) {
    const drawableObject = Drawable(x, y);
    const strokeObject = Stroke();
    const fillObject = Fill();
    const centerPointObject = CenterPoint();
    const rotatableObject = Rotatable();

    return {
        ...drawableObject,
        ...strokeObject,
        ...fillObject,
        ...centerPointObject,
        ...rotatableObject,
        w,
        h,
        strokeDraw: strokeObject.draw,
        fillDraw: fillObject.draw,
        getCenterPoint() {
            let x = this.x + this.w / 2;
            let y = this.y + this.h / 2;

            return { x, y };
        },
        draw(ctx) {
            const { x, y } = this;

            const paddingWidth = this.w / 2;
            const paddingHeight = this.h / 2;

            const x1 = x - paddingWidth;
            const x2 = x + paddingWidth;
            const y1 = y - paddingHeight;
            const y2 = y + paddingHeight;

            // debugger;

            this.strokeDraw(ctx);
            this.fillDraw(ctx);

            ctx.beginPath();
            ctx.rect(x1, y1, this.w, this.h);
            ctx.stroke();
            ctx.fill();
        }
    }
}

export function Image(x, y, w, h) {
    const drawableObject = Drawable(x, y);

    var internalHorizontalScale = 1;
    var internalVerticalScale = 1;

    return {
        ...drawableObject,
        w, 
        h,
        rawImage: null,
        rawPixels: null,
        set horizontalScale(value) {
            if (value < 1) throw new Error("Cannot set scale lower than 1");
            internalHorizontalScale = Math.round(value);
        },
        get horizontalScale() {
            return internalHorizontalScale;
        },
        set verticalScale(value) {
            if (value < 1) throw new Error("Cannot set scale lower than 1");
            internalVerticalScale = Math.round(value);
        },
        get verticalScale() {
            return internalVerticalScale;
        },
        get scaledWidth() {
            return Math.ceil(this.w / this.horizontalScale);
        },
        get scaledHeight() {
            return Math.ceil(this.h / this.verticalScale);
        },
        set scale(value) {
            this.horizontalScale = value;
            this.verticalScale = value;
        },
        get scale() {
            return this.verticalScale / this.horizontalScale * this.verticalScale;
        },
        get pixels() {
            return this.getPixels();
        },
        requestContext(ctx) {
            this.rawImage = ctx.getImageData(this.x, this.y, this.w, this.h);
            this.rawPixels = this.rawImage.data;
        },
        getPixelIndex(x, y) {
            return (y * this.w + x) * 4;
        },
        getRawPixel(x, y) {
            var index = this.getPixelIndex(x, y);
            var self = this;

            return {
                x,
                y,
                index,
                setColor(r, g, b, a = 255) {
                    self.rawPixels[index] = r;
                    self.rawPixels[index + 1] = g;
                    self.rawPixels[index + 2] = b;
                    self.rawPixels[index + 3] = a;
                },
                setGrayscale(value) {
                    this.setColor(
                        value,
                        value,
                        value
                    )
                }
            }
        },
        getPixelBlock(x, y, w, h) {
            var pixels = [];

            for (var i = 0; i < w; i++) {
                for (var j = 0; j < h; j++) {
                    pixels.push(this.getRawPixel(x + i, y + j));
                }
            }

            return {
                x,
                y,
                pixels,
                setColor(r, g, b, a = 255) {
                    pixels.forEach(pixel => pixel.setColor(r, g, b, a));
                },
                setGrayscale(value) {
                    pixels.forEach(pixel => pixel.setGrayscale(value));
                }
            }
        },
        getPixel(x, y) {
            return this.getPixelBlock(x * this.horizontalScale, y * this.verticalScale, this.horizontalScale, this.verticalScale);
        },
        fill(r, g, b, a = 255) {
            this.forEachPixel(pixel => pixel.setColor(r, g, b, a));
        },
        getPixels() {
            var pixels = [];

            this.forEachPixel(pixel => pixels.push(pixel));

            return pixels;
        },
        forEachPixel(fn) {
            for (var x = 0; x < this.scaledWidth; x++) {
                for (var y = 0; y < this.scaledHeight; y++) {
                    var pixel = this.getPixel(x, y);
                    fn(pixel);
                }
            }
        },
        draw(ctx) {
            ctx.putImageData(this.rawImage, this.x, this.y);
        }
    }
}

