async function requestJson(fileName) {
    const fullPath = `./json/${fileName}.json`;

    var response = await fetch(fullPath).then(r => r.json());
    return Object.values(response)[0];
}

export const getFishData = async () => requestJson("fish");
export const getBugData = async () => requestJson("bugs");
export const getSeaCreatureData = async () => requestJson("sea-creatures");
export const getVillagerData = async () => requestJson("villagers");
export const getFurnitureData = async () => requestJson("furniture");
export const getSongData = async () => requestJson("songs");