export function Screen(id, fps = 30) {
    var canvas = id instanceof HTMLElement ? id : document.getElementById(id);
    var ctx = canvas.getContext("2d");
    var timer;

    const rawPixel = ctx.createImageData(1, 1);
    const rawColor = rawPixel.data;

    var screenObject = {
        canvas,
        ctx,
        fps,
        objects: [],
        rawPixel,
        rawColor,
        active: false,
        beforeDraw: null,
        afterDraw: null,
        lastDraw: 0,
        currentDraw: 0,
        trueFps: 0,
        trueSpeed: 0,
        addObject(object) {
            this.objects.push(object);

            if (typeof object.requestScreen === "function") object.requestScreen(this);
            if (typeof object.requestContext === "function") object.requestContext(this.ctx);
        },
        removeObject(object) {
            var index = this.objects.indexOf(object);
            this.objects.splice(index, 1);
        },
        clearObjects() {
            this.objects = [];
        },
        setPixelColor(r = 0, g = 0, b = 0, a = 255) {
            this.rawColor[0] = r;
            this.rawColor[1] = g;
            this.rawColor[2] = b;
            this.rawColor[3] = a;
        },
        drawPixel(x, y) {
            this.ctx.putImageData(this.rawPixel, x, y);
        },
        drawObject(object) {
            if (typeof object.beforeDraw === "function") object.beforeDraw(this.ctx);
            if (typeof object.drawRotation === "function") object.drawRotation(this.ctx);

            if (typeof object.drawRotation === "function") object.drawRotation(this.ctx);
            else object.draw(this.ctx);

            if (typeof object.endRotation === "function") object.endRotation(this.ctx);
            if (typeof object.afterDraw === "function") object.afterDraw(this.ctx);
        },
        draw() {
            this.lastDraw = this.currentDraw;
            this.currentDraw = Date.now();

            this.trueFps = 1000 / (this.currentDraw - this.lastDraw);
            this.trueSpeed = this.trueFps / this.fps;

            this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);

            if (typeof this.beforeDraw === "function") this.beforeDraw(this.ctx);

            this.objects.forEach(object => this.drawObject(object));

            if (typeof this.afterDraw === "function") this.afterDraw(this.ctx);
        },
        start() {
            const drawFrame = () => {
                this.draw();

                timer = setTimeout(drawFrame, 1000 / this.fps);
            }

            this.active = true;
            drawFrame();
        },
        stop() {
            clearTimeout(timer);
            this.active = false;
        }
    }

    return screenObject;
}

export function Drawable(x, y) {
    return {
        x,
        y,
        beforeDraw: null,
        afterDraw: null,
        requestScreen: null,
        requestContext: null,
        setPosition(x, y) {
            this.x = x;
            this.y = y;
        },
    }
}

export function CenterPoint(centerPointXFunction = null, centerPointYFunction = null) {
    const object = {
        get centerPointX() { return this.getCenterPoint().x; },
        get centerPointY() { return this.getCenterPoint().y; },
        getCenterPoint() {
            let x = this.getCenterPointX();
            let y = this.getCenterPointY();

            return {
                x,
                y,
            }
        },
        getCenterPointX() {
            return this.x;
        },
        getCenterPointY() {
            return this.y;
        }
    }

    if (typeof centerPointXFunction === "function") object.getCenterPointX = centerPointXFunction;
    if (typeof centerPointYFunction === "function") object.getCenterPointY = centerPointYFunction;

    return object;
}

export function Rotatable(rotation) {
    return {
        ...Drawable(0, 0),
        offsetRotationX: 0,
        offsetRotationY: 0,
        rotation,
        drawRotation(ctx) {
            if (typeof this.getCenterPoint !== "function") throw new Error("A rotatable object should have a center point. Try inheriting from 'CenterPoint' or override the 'getCenterPoint' method returning the x and y of the center point.")

            this.startRotation(ctx);
            this.draw(ctx);
            this.endRotation(ctx);
        },
        startRotation(ctx) {
            ctx.save();
            var rad = this.rotation * Math.PI / 180;

            var { x, y } = this.getCenterPoint();
            var rc = y / x;
            var currentAngle = Math.atan(rc);
            var rotationRad = -this.rotation * 0.01745;

            var radius = Math.sqrt(x ** 2 + y ** 2);

            var newAngle = currentAngle - rotationRad;
            var nexX = Math.cos(newAngle) * radius;
            var nexY = Math.sin(newAngle) * radius;

            var diffX = x - nexX;
            var diffY = y - nexY;

            this.offsetRotationX = diffX;
            this.offsetRotationY = diffY;

            ctx.translate(this.offsetRotationX, this.offsetRotationY);
            ctx.rotate(rad);
        },
        endRotation(ctx) {
            ctx.restore();
        }
    }
}

export function Stroke() {
    return {
        strokeWidth: 1,
        strokeStyle: "#000000",
        setStrokeWidth(width) {
            this.strokeWidth = width;
        },
        setStrokeStyle(style) {
            this.strokeStyle = style;
        },
        setStrokeRgb(r = 0, g = 0, b = 0) {
            r = r.toString(16);
            g = g.toString(16);
            b = b.toString(16);

            this.setStrokeStyle("#" + r + g + b);
        },
        draw(ctx) {
            ctx.strokeStyle = this.strokeStyle;
            ctx.lineWidth = this.strokeWidth;
        }
    }
}

export function Fill() {
    return {
        fillStyle: "#000000",
        setFillStyle(style) {
            this.fillStyle = style
        },
        setFillRgb(r = 0, g = 0, b = 0, a = 255) {
            var hexR = Math.floor(r).toString(16).padStart(2, "0");
            var hexG = Math.floor(g).toString(16).padStart(2, "0");
            var hexB = Math.floor(b).toString(16).padStart(2, "0");
            var hexA = Math.floor(a).toString(16).padStart(2, "0");

            var hexColor = "#" + hexR + hexG + hexB + hexA;

            this.setFillStyle(hexColor);
        },
        draw(ctx) {
            ctx.fillStyle = this.fillStyle;
        }
    }
}