<button on:click={toggle}>
    {#if $donateMultiple}
        Stop donating
    {:else}
        Donate multiple
    {/if}
</button>

<script>
// Stores
import { donateMultiple } from "@/js/store.js";

// Functions
function toggle() {
    $donateMultiple = !$donateMultiple;
}
</script>

<style lang="scss">

button {
    display: block;
    margin: 24px auto;
    
    background-color: #ff8e59;
    border-radius: 5px;
    box-shadow: 0 0 9px #b4b4b4;
    color: #ffffff;
    font-weight: 500;
    transform: rotate(1deg);
    outline: 0;
    border: 2px solid #583c2f;
}

</style>