<div class="furnitureAdvert">
    <header>
        <img src="./img/nook-shopping.png" alt="">
    </header>
    <main>
        <div>
            <div class="imageContainer">
                <img src={furniture.image} alt="Image of {furniture.name}">
            </div>
            <div class="detailsContainer">
                <h3>{furniture.name}</h3>
            </div>
        </div>
    </main>
    <footer>

    </footer>
</div>

<script>
// Props
export var furniture = {};
</script>

<style lang="scss">

.furnitureAdvert {
    $imageSize: 100px;
    $imageBorderSize: 16px;

    border-radius: 10px;
    
    header {
        height: 48px;
        width: 100%;

        background-color: #f7e640;
        background-image: url(../img/nook-shopping-header.png);
        border-top-left-radius: inherit;
        border-top-right-radius: inherit;

        img {
            height: calc(80% - 8px);

            padding: 4px;
            margin-top: 4px;
            margin-right: 4px;
            float: right;

            background: #ffffff;
            border-radius: 50%;
        }
    }

    main {
        padding: 16px;
        margin: -1px 0;
        position: relative;

        background-color: #f1e369;
        background-image: url(../img/nook-shopping-body.png);

        .imageContainer {
            height: $imageSize;
            width: $imageSize;

            margin-top: -40px;

            background-color: #caffdf;
            border-radius: 20px;
            border: $imageBorderSize solid #ebfff3;

            img {
                height: 100%;
                width: 100%;
            }
        }

        .detailsContainer {
            width: calc(100% - #{$imageSize + 2 * $imageBorderSize} - 32px);
            
            position: absolute;
            top: 0;
            right: 0;
        }

        .variantGrid {
            // height: $imageSize + 2 * $imageBorderSize;
            width: $imageSize + 2 * $imageBorderSize;

            padding: 8px 0;
            display: grid;
            grid-gap: 4px;
            grid-template-columns: 1fr 1fr;

            .variant {
                width: 100%;
                height: 100%;

                background-color: #caffdf;
                border: 4px solid #ebfff3;

                img {
                    width: 80%;
                    height: 80%;

                    position: relative;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }
            }
        }
    }
}

</style>