<div class="mainGrid">
    {#each list as item, index}
        <JournalItem {item} {index} />
    {/each}
</div>

<script>
// Components
import JournalItem from "@/components/JournalItem.svelte";

// Props
export var items = [];

if (!Array.isArray(items)) items = [items];

// Data
var list = [];
var scrollY = 0;

// On create
(async function() {
    var resolvedPromises = await Promise.all(items);
    list = resolvedPromises.flat(1);
})();
</script>

<style lang="scss">

.mainGrid {
    width: 95%;
    max-width: 935px;

    margin: auto;
    display: grid;
    grid-auto-rows: 64px;
    grid-template-columns: repeat(auto-fit, 64px);
    grid-gap: 1px;
    justify-content: center;
}

button {
    display: block;
    margin: 16px auto;
    transform: rotate(2deg);

    background-color: #ffad60;
    color: #000000;
    border: 0;
    box-shadow: 0 0 4px #00000033;
    cursor: pointer;
}

</style>