import { Drawable } from "./screen.js";

export function ParticleSystem(x, y, w = null, h = null) {
    const drawableObject = Drawable(x, y);

    return {
        ...drawableObject,
        w,
        h,
        frequency: 30,
        framesSinceLastParticle: Infinity,
        activeParticles: [],
        particlePool: [],
        particleFunction: null,
        summonCheckFunction: self => self.framesSinceLastParticle > self.frequency,
        requestScreen(screen) {
            if (this.w === null) this.w = screen.canvas.width;
            if (this.h === null) this.h = screen.canvas.height;

            this.screen = screen;
        },
        clearParticles() {
            this.activeParticles = [];
        },
        addToPool(particle) {
            this.particlePool.push(particle);
        },
        removeFromPool(particle) {
            var index = this.particlePool.indexOf(particle);
            this.particlePool.splice(index, 1);
        },
        drawFromPool() {
            var index = Math.floor(Math.random() * this.particlePool);
            return this.particlePool[index];
        },
        drawMultipleFromPool(amount) {
            var drawnParticles = [];

            for (var i = 0; i < amount; i++) {
                drawnParticles.push(this.drawFromPool());
            }

            return drawnParticles;
        },
        finalizeParticle(particle) {
            if (typeof particle.onCreate === "function") particle.onCreate(this);

            return particle;
        },
        checkSummon() {
            if (this.summonCheckFunction(this)) {
                this.framesSinceLastParticle = 0;

                let newParticles = this.particleFunction();
                if (newParticles.length === undefined) {
                    this.activeParticles.push(this.finalizeParticle(newParticles));
                } else {
                    this.activeParticles = [
                        ...this.activeParticles,
                        ...newParticles.map(particle => this.finalizeParticle(particle)),
                    ]
                }
            }
        },
        destroyParticle(particle) {
            if (typeof particle.onDestroy === "function") particle.onDestroy();

            var index = this.activeParticles.indexOf(particle);
            this.activeParticles.splice(index, 1);
        },
        drawParticles(ctx) {
            this.activeParticles.forEach(particle => {
                particle.age++;

                particle.tick(this);
                this.screen.drawObject(particle);

                if (particle.age > particle.maxAge) this.destroyParticle(particle);
            });
        },
        draw(ctx) {
            if (typeof this.particleFunction !== "function") return;

            this.checkSummon();
            this.drawParticles(ctx);
            this.framesSinceLastParticle++;
        }
    }
}

export function Particle(options) {
    const drawableObject = Drawable(0, 0);

    return {
        ...drawableObject,
        age: 0,
        maxAge: 1000,
        onCreate: null,
        onDestroy: null,
        ...options,
    }
}