<div class="furnitureItem" on:click={openDetails}>
    <img src={item.image} alt="" loading="lazy">
</div>

<script>
// Props
export var item = {};
export var open = false;

// Functions
function openDetails() {
    open = true;
}
</script>

<style lang="scss">

.furnitureItem {
    height: 100%;
    width: 100%;

    img {
        $size: 80%;

        width: $size;
        height: $size;

        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

</style>