<div class="background {availableNow} {donated ? "donated" : ""} {$donateMultiple ? "selecting" : ""}" on:click={openDetails}>
    {#if donated}
        <img class="donatedIcon" src="./img/donated.svg" alt="donated" />
    {:else if leaving}
        <img class="donatedIcon" src="./img/leaving.svg" alt="donated" />
    {/if}
    <img class="icon" loading="lazy" src={item.icon} alt={item.name} />
</div>

<script>
import { availability, isLeaving } from "@/js/timeUtils.js";
import { donateMultiple } from "@/js/store.js";

// Store
import { donatedList } from "@/js/store.js";

// Components
import ItemDetails from "@/components/ItemDetails.svelte";

// Props
export var open;
export var item;

// Computed
$: availableNow = availability(item);
$: donated = $donatedList.includes(item.id);
$: leaving = isLeaving(item);

// Functions
function openDetails() {
    if ($donateMultiple) toggleDonated()
    else open = true;
}

function onClickJournalItem() {
    if (donateMultiple) {
        toggleDonated();
    } else {
        openDetails();
    }
}

function toggleDonated() {
    if ($donatedList.includes(item.id)) {
        var index = $donatedList.indexOf(item.id);
        $donatedList.splice(index, 1);
        $donatedList = $donatedList;
    } else {
        $donatedList = [
            ...$donatedList,
            item.id,
        ]
    }
}
</script>

<style lang="scss">

.background {
    height: 100%;
    width: 100%;

    &.selecting:not(.donated) .icon {
        animation: wiggle 750ms linear infinite;
    }

    &.availableMonth {
        background-color: #96ffdc;
    }

    &.availableTime {
        background-color: #ffb85c;

        &:not(.donated):not(.selecting) .icon {
            animation: availableTime 3500ms linear infinite;
        }
    }
}

.icon {
    $size: 80%;

    width: $size;
    height: $size;

    display: block;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.donatedIcon {
    $size: 14px;

    height: $size;
    width: $size;

    position: absolute;
    left: 2px;
    top: 2px;
}

@keyframes wiggle {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
    }

    40% {
        transform: translate(-50%, -50%) rotate(0deg);
    }

    60% {
        transform: translate(-50%, -50%) rotate(-10deg);
    }

    80% {
        transform: translate(-50%, -50%) rotate(10deg);
    }

    100% {
        transform: translate(-50%, -50%) rotate(0deg);
    }
}

@keyframes availableTime {
    0% {
        transform: translate(-50%, -50%) scale(1) rotate(0deg);
    }

    80% {
        transform: translate(-50%, -50%) scale(1) rotate(0deg);
    }

    84% {
        transform: translate(-50%, -50%) scale(1.03125) rotate(20deg);
    }

    88% {
        transform: translate(-50%, -50%) scale(1.0625) rotate(-20deg);
    }

    92% {
        transform: translate(-50%, -50%) scale(1.09375) rotate(20deg);
    }

    96% {
        transform: translate(-50%, -50%) scale(1.125) rotate(0);
    }

    100% {
        transform: translate(-50%, -50%) scale(1) rotate(0deg);
    }
}

</style>