<div class="itemDetail" on:click|self={close}>
    <svelte:component this={typeComponent} {item} />
</div>

<script>
// Components
import SpecimenDetail from "@/components/ItemDetails/SpecimenDetail.svelte";
import VillagerDetail from "@/components/ItemDetails/VillagerDetail.svelte";
import FurnitureDetail from "@/components/ItemDetails/FurnitureDetail.svelte";

// Props
export var open = false;
export var item = {};

// Data
const typeComponentMap = {
    fish: SpecimenDetail,
    bug: SpecimenDetail,
    seaCreature: SpecimenDetail,
    villager: VillagerDetail,
    furniture: FurnitureDetail,
}

// Computed
$: itemType = item.type;
$: typeComponent = typeComponentMap[itemType];

// Functions
function close() {
    open = false;
}
</script>

<style lang="scss">

.itemDetail {
    height: 100%;
    width: 100%;

    position: fixed;
    top: 0;
    left: 0;
    z-index: 4;

    background-color: #cecece56;
    overflow-y: auto;
}

</style>